import React, { useEffect, useRef } from "react";
import "./message.css";

const Message = ({ text, sender, isSentByUser, img }) => {
    const messageRef = useRef(null);

    useEffect(() => {
        // Cun ti phn t tin nhn cui cng khi component c render hoc tin nhn mi c thm vo
        if (messageRef.current) {
            messageRef.current.scrollIntoView({ behavior: "smooth" });
        }
    }, [text]); // Theo di thay i ca tin nhn  cun ti tin nhn mi

    // Kim tra nu tin nhn l nh (bt u bng "data:image/")
    const isImageMessage = text.startsWith("data:image/");

    return (
        <div ref={messageRef} className={`messageContainer ${isSentByUser ? "sent" : "received"}`}>
            {/* Hin th avatar ca ngi gi tin nhn i phng */}
            {!isSentByUser && (
                <div className="avatar">
                    <img src="img/p1.jpg" alt="avatar"/>
                </div>
            )}
            {/* Nu tin nhn l nh, hin th nh, ngc li hin th vn bn */}
            {isImageMessage ? (
                <div className="imageMessage">
                    <img src={text} alt="Sent as image" />
                </div>
            ) : (
                <div className={`messageBox ${isSentByUser ? "sentMessage" : "receivedMessage"}`}>
                    <p className="messageText">{text}</p>
                    {/* Hin th tn ngi gi tin nhn i phng */}
                    {!isSentByUser && <p className="messageSender">{sender}</p>}
                </div>
            )}
        </div>
    );
};

export default Message;
