import styles from './styles.module.css';
import {useLocation, useNavigate} from 'react-router-dom';
import React from "react";
import {FaArrowLeft} from "react-icons/fa";
import { useSelector } from 'react-redux'; // Import useSelector


function Information() {

    const navigate = useNavigate();
    // Ly thng tin ngi dng t Redux store
    // useSelector  truy cp state ca Redux, trong trng hp ny l thng tin ngi dng
    const user = useSelector((state) => state.user?.infor || {});
    console.log(user); // Xem gi tr ca user  xc nhn thng tin

    const handleLogout = () => {


        navigate('/home');
    };
    const informationForward = () =>{
        navigate('/info');
    }

    const protectAccForward = () =>{
        navigate('/acc_protect');
    }


    const historyForward = () =>{
        navigate('/history');
    }


    const changeEmail=()=>{
        navigate('/email')
    }
    const changePassword=()=>{
        navigate('/pass')
    }

    const changeNumber=()=>{
        navigate('/number')

    }

    const changeInfo=()=>{
        navigate('/personalinfo')

    }

    // nh ngha hm maskPassword
    const maskPassword = (password) => {
        return '*'.repeat(password.length);
    };



    return (
        <div className={styles.contain}>
        <div className={styles.main}>
            <div className={styles.left}>
                <div className={styles.container}>
                    <div className={styles.avatar}>

                        <div className={styles['back-arrow']} onClick={() => navigate('/home')}>
                            &#8592; {}
                        </div>
                        <img
                            className={styles.avatar}
                            src="https://www.w3schools.com/howto/img_avatar.png"
                            alt="Avatar"
                        /></div>
                </div>
                <div className={styles.function}>
                    <div className={styles['function-item']} onClick={informationForward}>

                        <h4>Thng tin ti khon</h4>
                        <span>Qun l thng tin ng nhp v thng tin c nhn</span>

                    </div>

                    <div className={styles['function-item']} onClick={protectAccForward}>
                        <h4>Bo v ti khon</h4>
                        <span>H tr bo v ti khon</span>
                    </div>
                    <div className={styles['function-item']} onClick={historyForward}>
                        <h4>Nht k hot ng</h4>
                        <span>Lch s hot ng ca ti khon</span>
                    </div>


                </div>
            </div>
            <div className={styles.right}>
                <div className={styles.title}>
                    <h2>Thng Tin Ti Khon</h2>
                </div>
                <div className={styles['title-info']}>
                    <h3>Thng tin ng nhp</h3>
                    <div className={styles['info-item']}>
                        <label htmlFor="number">Ti khon :</label>
                    </div>
                    <div className={styles['info-item']}>
                        <label htmlFor="password">Mt khu : </label>
                        <button onClick={changePassword}>Thay i</button>
                    </div>
                </div>
                <div className={styles['title-info']}>
                    <h3>
                        Thng tin c nhn
                        <button onClick={changeInfo}>Thay i</button>
                    </h3>
                    <div className={styles['info-item']}>
                        <label htmlFor="name">H v Tn :</label>
                    </div>
                    <div className={styles['info-item']}>
                        <label htmlFor="sex">Gii tnh :</label>
                    </div>
                    <div className={styles['info-item']}>
                        <label htmlFor="dob">Ngy sinh :</label>
                    </div>
                    <div className={styles['info-item']}>
                        <label htmlFor="address">a ch :</label>
                    </div>
                </div>
            </div>
        </div>
        </div>

    );
}

export default Information;
