import { createSlice } from "@reduxjs/toolkit";

const userSlice = createSlice({
  name: "user",
  initialState: {
    infor: { name: "", email: "", friends: [], groups: [], messages: [] },
    status: "UnAuth",
  },
  reducers: {
    setName: (state, action) => {
      state.infor.name = action.payload;
      state.status = "Auth";
    },
    setEmail: (state, action) => {
      state.infor.email = action.payload;
      state.status = "Auth";
    },
    setFriends: (state, action) => {
      const { name, type, actionTime, avatarUrl } = action.payload.item;
      const newFriend = {
        name,
        listmessage: [], // lu tin nhn ca ngi dng
        type,
        actionTime,
        avatarUrl,
      };
      state.infor.friends.push(newFriend);
      state.status = "Auth";
    },
    // lu tin nhn vo danh sch tin nhn ca ngi dng
    saveMessage: (state, action) => {
      // Ly tn ngi dng v tin nhn t action
      const { name, mess } = action.payload;
      // Tm v tr ca ngi dng trong danh sch bn b
      const friendIndex = state.infor.friends.findIndex((f) => f.name === name);

      // Nu ngi dng tn ti trong danh sch bn b
      if (friendIndex !== -1) {
        const friend = state.infor.friends[friendIndex]; // Ly thng tin ngi dng
        if (mess && mess.text && mess.sender) {
          // Kim tra xem tin nhn c d liu khng
          const isCurrentUser = mess.sender === state.infor.email; // Kim tra xem ngi gi c phi l ngi ng nhp khng
          const message = {
            text: mess.text,
            sender: mess.sender,
            type: isCurrentUser ? "currentUser" : "otherUser",
            time: new Date().toISOString(),
          };
          friend.listmessage.push(message); // Thm tin nhn vo danh sch tin nhn ca ngi dng
        }
        state.infor.friends[friendIndex] = { ...friend }; // Cp nht thng tin ngi dng
      }
    },

    clearMessage: (state, action) => {
      const { name } = action.payload;
      const friend = state.infor.friends.find((f) => f.name === name);
      if (friend) {
        friend.listmessage = []; // Clear messages for a specific friend
      }
    },
    setGroups: (state, action) => {
      const { name, type, actionTime } = action.payload.item;
      const newGroup = {
        nameGroup: name,
        listmessage: [],
        type: type || 1,
        actionTime: actionTime || "",
      };
      state.infor.groups.push(newGroup);
      state.status = "Auth";
    },
    saveGroupMess: (state, action) => {
      const { nameGroup, messGroup } = action.payload;
      const group = state.infor.groups.find((g) => g.nameGroup === nameGroup);
      if (group) {
        // messGroup.forEach((message) => {
        const isSentByUser = messGroup.isSentByUser;
        group.listmessage = [
          ...group.listmessage,
          {
            text: messGroup.text,
            isSentByUser,
          },
        ];
        // });
      }
    },
    clearGroupMess: (state, action) => {
      const { nameGroup } = action.payload;
      const group = state.infor.groups.find((g) => g.nameGroup === nameGroup);
      if (group) {
        group.listmessage = []; // Clear messages for a specific group
      }
    },
    logout: (state, action) => {
      return {
        ...state,
        infor: { name: "", email: "", friends: [], groups: [] },
        status: `${"UnAuth"}`,
      };
      // state.infor = { name: "", email: "", friends: [], groups: [] };
      // state.status = "UnAuth";
    },
    saveImageURL: (state, action) => {
      const { name, imageUrl } = action.payload;
      const friendIndex = state.infor.friends.findIndex((f) => f.name === name);
      if (friendIndex !== -1) {
        const friend = state.infor.friends[friendIndex];
        const imageMessage = {
          imageUrl: imageUrl, // Lu URL ca nh
          sender: state.infor.name,
          type: "currentUser",
          time: new Date().toISOString(),
        };
        friend.listmessage.push(imageMessage);
        state.infor.friends[friendIndex] = { ...friend };
      }
    },
  },
});

export const {
  setName,
  setEmail,
  setFriends,
  setGroups,
  saveMessage,
  clearMessage,
  saveGroupMess,
  clearGroupMess,
  saveImageURL,
  logout,
} = userSlice.actions;

export default userSlice.reducer;
