import React, { useContext, useEffect, useState } from "react";
import Friend from "../friend/friend";
import "./list.css";
import { useDispatch, useSelector } from "react-redux";
import { WebsocketContext } from "../../../socket/WebsocketContent";
import {
  GET_PEOPLE_CHAT_MES,
  GET_ROOM_CHAT_MES,
  Logout,
  SEND_CHAT,
  CREATE_ROOM,
  JOIN_ROOM,
} from "../../../api/action";
import {
  saveMessage,
  saveGroupMess,
  logout,
  setFriends,
} from "../../../store/userSlice";
import ShowGroup from "../../../components/group/showgroup/GroupShow";
import { Link, useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowRightFromBracket,
  faMessage,
} from "@fortawesome/free-solid-svg-icons";

const List = (props) => {
  const [, respone, sender] = useContext(WebsocketContext);
  const dispatch = useDispatch();
  const infor = useSelector((state) => state.reducer);
  const friends = infor.user.infor.friends;
  const groups = infor.user.infor.groups;
  const userInfo = infor.user.infor;
  const all = [...friends, ...groups];
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const handleLogout = () => {
    const logoutAction = Logout();
    sender(logoutAction);
  };
  useEffect(() => {
    all.forEach((item) => {
      if (item.type === 0) {
        // nu l ngi bn
        const getPeopleChatMess = GET_PEOPLE_CHAT_MES(item.name); // ly tin nhn gia 2 ngi
        sender(getPeopleChatMess); // gi yu cu ly tin nhn gia 2 ngi
      } else if (item.type === 1) {
        // nu l nhm
        const getRoomChatMess = GET_ROOM_CHAT_MES(item.nameGroup);
        sender(getRoomChatMess); // gi yu cu ly tin nhn trong nhm
      }

      return () => {};
    });
  }, []);
  // hm ny s x l khi click vo 1 ngi bn v hin ra tin nhn gia 2 ngi
  const handleItemOnClick = (item) => {
    if (item.type === 0) {
      // nu l ngi bn
      const getPeopleChatMess = GET_PEOPLE_CHAT_MES(item.name); // ly tin nhn gia 2 ngi
      sender(getPeopleChatMess); // gi yu cu ly tin nhn gia 2 ngi
    } else if (item.type === 1) {
      // nu l nhm
      const getRoomChatMess = GET_ROOM_CHAT_MES(item.nameGroup);
      sender(getRoomChatMess); // gi yu cu ly tin nhn trong nhm
    }
    props.setChatUser(item);
  };

  // hm ny s x l khi nhn c tin nhn t server thng qua hm sender
  const handleGetPeopleChatMess = (payload) => {
    payload.data.forEach((item) => {
      // duyt qua tng tin nhn
      const { name, to, mes } = item;
      const isSentByUser = name === infor.user.infor.email; // kim tra xem ngi gi c phi l ngi dng hin ti khng
      dispatch(
        saveMessage({
          name: isSentByUser ? to : name, // nu l ngi gi th lu tn ngi nhn, ngc li lu tn ngi gi
          mess: { text: mes, sender: name, isSentByUser },
        })
      );
    });
  };

  const handleGetRoomChatMess = (payload) => {
    payload.data.chatData.forEach((item) => {
      const { name, mes } = item;
      const isSentByUser = name === userInfo.email;
      dispatch(
        saveGroupMess({
          nameGroup: payload.data.name,
          messGroup: { text: mes, sender: name, isSentByUser },
        })
      );
    });
  };

  const handleSendChat = (payload) => {
    const check = friends.every((item) => item.name !== payload.data.name);
    if (check) {
      const item = { name: payload.data.name, type: 0, actionTime: "" };
      dispatch(setFriends({ item }));
      sender(SEND_CHAT(payload.data.name, ""));
    }
  };

  useEffect(() => {
    if (respone && respone.status === "success") {
      switch (respone.event) {
        case "SEND_CHAT":
          handleSendChat(respone);
          break;
        case "GET_PEOPLE_CHAT_MES":
          handleGetPeopleChatMess(respone);
          break;
        case "LOGOUT":
          dispatch(logout());
          navigate("/");
          break;
      }
    }
  }, [respone]);

  const findfriend = () => {
    let value = name;
    const send_chat = SEND_CHAT(value, "add friend");
    setName((pre) => "");
    sender(send_chat);
  };

  const joingroup = () => {
    let value = name;
    const join_room = JOIN_ROOM(value);
    setName((pre) => "");
    sender(join_room);
  };

  const creategroup = () => {
    let value = name;
    const create_room = CREATE_ROOM(value);
    setName((pre) => "");
    sender(create_room);
  };

  const getLatestMessage = (name) => {
    let latestMessage = "";

    // Tm kim bn b c tn l 'name'
    const friend = friends.find((f) => f.name === name);
    if (friend) {
      const lastMessage = friend.listmessage[friend.listmessage.length - 1];
      if (lastMessage) {
        // Kim tra xem tin nhn c gi bi ngi dng hin ti hay khng
        const isSentByUser = lastMessage.sender === userInfo.name;
        // To chui tin nhn da trn ngi gi v ngi nhn
        latestMessage = `${isSentByUser ? "Bn" : lastMessage.sender}: ${
          lastMessage.text
        }`;
      }
    }
    // Tm kim nhm c tn l 'name'
    const group = groups.find((g) => g.nameGroup === name);
    if (group) {
      const lastMessage = group.listmessage[group.listmessage.length - 1];
      if (lastMessage) {
        // Kim tra xem tin nhn c gi bi ngi dng hin ti hay khng
        const isSentByUser = lastMessage.sender === userInfo.name;
        // To chui tin nhn da trn ngi gi v ngi nhn
        latestMessage = `${lastMessage.text}`;
        // ${isSentByUser ? "Bn" : lastMessage.sender}:
      }
    }

    return latestMessage;
  };

  return (
    <div className="list">
      <div className="list_header">
        <div className="img">
          <Link to="/info">
            <img
              src="https://www.w3schools.com/howto/img_avatar.png"
              alt="avatar"
            />
          </Link>
          <div className="name">
            <span>{userInfo.name}</span>
          </div>
        </div>
        <div className="item log-out" onClick={handleLogout}>
          <div className="icon">
            <FontAwesomeIcon icon={faArrowRightFromBracket} />
          </div>
        </div>
      </div>
      <div className="search">
        <div className="searchForm">
          <input
            onChange={(e) => {
              setName(e.target.value);
            }}
            value={name}
            type="text"
            placeholder="Search..."
          />
        </div>
      </div>
      <div className="allbtn">
        <button className="btn left" onClick={findfriend}>
          Tm bn
        </button>
        <button className="btn right" onClick={joingroup}>
          Vo nhm
        </button>
        <button className="btn middle" onClick={creategroup}>
          To nhm
        </button>
      </div>
      <div className="chatList">
        {all &&
          all
            .filter((item) => item.type !== 0 || item.name !== userInfo.name) // Lc ra nhng ngi khng phi l ngi dng hin ti
            .map((item, index) => {
              const uniqueKey =
                item.type === 0
                  ? `friend-${item.name}`
                  : `group-${item.nameGroup}`;
              return item.type === 0 ? (
                <Friend
                  key={uniqueKey}
                  img={item.img}
                  name={item.nameGroup || item.name}
                  time={item.time}
                  message={getLatestMessage(item.name)}
                  unread={item.unread}
                  onClick={() => handleItemOnClick(item)}
                />
              ) : (
                <ShowGroup
                  key={uniqueKey}
                  nameGroup={item.nameGroup}
                  messGroup={getLatestMessage(item.nameGroup)}
                  onClick={() => handleItemOnClick(item)}
                />
              );
            })}
      </div>
    </div>
  );
};

export default List;
