import React, {
  useContext,
  useEffect,
  useRef,
  useState,
  useImperativeHandle,
  forwardRef,
} from "react";
import "./chat.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBars,
  faFaceSmile,
  faPaperclip,
  faPaperPlane,
  faPhone,
  faVideo,
} from "@fortawesome/free-solid-svg-icons";
import { WebsocketContext } from "../../../socket/WebsocketContent";
import { SEND_CHAT, GET_PEOPLE_CHAT_MES } from "../../../api/action";
import { useDispatch, useSelector } from "react-redux";
import { saveMessage, saveImageURL } from "../../../store/userSlice";
import Message from "../message/message";
import EmojiPicker from "emoji-picker-react";

const Chat = (props, ref) => {
  const [isReady, respone, sender] = useContext(WebsocketContext);
  const dispatch = useDispatch();
  const { name: loggedInUserName } = useSelector(
    (state) => state.reducer.user.infor
  ); // Ly email ngi ang ng nhp
  const { friends } = useSelector((state) => state.reducer.user.infor);
  const [currentFriend, setCurrentFriend] = useState(null);
  const [newMessage, setNewMessage] = useState("");
  const [selectedEmoji, setSelectedEmoji] = useState("");
  const [messages, setMessages] = useState([]);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false); // State  qun l hin th bng chn emoji
  const [selectedFile, setSelectedFile] = useState(null); //
  const [image, setImage] = useState({
    file: null,
    url: "",
  });

  useEffect(() => {
    const friend = friends.find((item) => item.name === props.friend.name);
    setCurrentFriend(friend);
  }, [props.friend, friends]);

  const handleGetPeopleChatMess = (payload) => {
    console.log("Raw Data from Server:", payload.data);
    const listmess = payload.data.reverse().map((item) => ({
      text: item.mes,
      sender: item.name,
    }));

    // Debugging to check email and message sender
    console.log("Logged In User Name:", loggedInUserName);
    console.log("Current Friend Name:", currentFriend?.name); // y l ngi bn hin ti m bn ang chat cng
    console.log("Messages from Server:", listmess);

    // Lc v cp nht tin nhn cho ng vi ngi ang ng nhp v i phng
    const filteredMessages = listmess.map((item) => ({
      text: item.text,
      // kim tra tin nhn c phi ca ngi gi khng
      isSentByUser: item.sender === loggedInUserName,
    }));
    // Lu tin nhn vo danh sch tin nhn ca ngi dng
    dispatch(
      saveMessage({
        name: currentFriend.name,
        messages: filteredMessages,
      })
    );
    // Cp nht tin nhn hin th trn giao din theo 2 bn ngi gi v nhn

    setMessages(filteredMessages);
  };

  const handleSendChatResponse = (payload) => {
    const isSentByUser = payload.data.name === loggedInUserName; // Kim tra xem tin nhn c phi ca ngi gi khng
    console.log(
      `New Message: ${payload.data.mes}, Sender: ${payload.data.name}, isSentByUser: ${isSentByUser}`
    );
    const newChat = [
      ...messages,
      {
        text: payload.data.mes,
        isSentByUser: payload.data.name === loggedInUserName, // Kim tra xem tin nhn c phi ca ngi gi khng
      },
    ];
    setMessages(newChat);
    setNewMessage("");
  };

  useEffect(() => {
    if (respone && respone.status === "success") {
      switch (respone.event) {
        case "SEND_CHAT":
          handleSendChatResponse(respone);
          break;
        case "GET_PEOPLE_CHAT_MES":
          handleGetPeopleChatMess(respone);
          break;
        default:
          break;
      }
    }
  }, [respone]);

  const handleSendMessages = async () => {
    if (newMessage.trim() || selectedEmoji !== "" || selectedFile) {
      // Kim tra xem tin nhn hoc emoji c d liu khng
      let messageToSend = newMessage.trim(); // Xa khong trng  u v cui chui

      // Nu c emoji c chn th kt hp vo tin nhn
      if (selectedEmoji !== "") {
        messageToSend += selectedEmoji;
        console.log("Message with Emoji:", messageToSend);
      }

      sender(SEND_CHAT(props.friend.name, messageToSend));
      const newChat = [
        ...messages,
        { text: messageToSend, isSentByUser: true },
      ];
      setMessages(newChat);
      setNewMessage(""); // Xa ni dung tin nhn sau khi gi

      // Lu tin nhn v emoji vo Redux
      console.log("Message anh file:", messageToSend, selectedFile);
      dispatch(
        saveMessage({
          name: currentFriend.name,
          mess: {
            text: messageToSend,
            sender: loggedInUserName,
          },
        })
      );
      // Gi nh nu c
      if (selectedFile) {
        try {
          const formData = new FormData();
          formData.append("image", selectedFile);
          formData.append("sender", loggedInUserName);
          formData.append("receiver", currentFriend.name);

          // Gi nh ln server thng qua WebSocket
          const response = await sender(
            SEND_CHAT(props.friend.name, "", formData)
          );

          // Lu tin nhn nh vo Redux
          const imageUrl = response.data.imageUrl; // URL ca nh sau khi lu trn server
          dispatch(
            saveImageURL({
              name: currentFriend.name,
              imageUrl: imageUrl,
            })
          );

          // Cp nht tin nhn hin th trn giao din
          const newChatWithImage = [
            ...messages,
            { text: imageUrl, isSentByUser: true }, // Hin th nh bng URL
          ];
          setMessages(newChatWithImage);
        } catch (error) {
          console.error("Error sending image:", error);
          // X l li khi gi nh (nu cn)
        } finally {
          setSelectedFile(null); // t li selectedFile v null sau khi gi
        }
      }
    }
  };
  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault(); // Ngn chn hnh ng mc nh ca Enter (thng l submit form)
      handleSendMessages(); // Gi hm gi tin nhn
    }
  };

  const inputRef = useRef();
  useImperativeHandle(ref, () => ({
    handleFileChange, // Hm x l khi chn nh t my tnh
    clearInput() {
      setNewMessage("");
    },
  }));

  const handleEmoji = (e) => {
    if (newMessage.trim() === "") {
      sender(SEND_CHAT(props.friend.name, e.emoji));
      const newChat = [...messages, { text: e.emoji, isSentByUser: true }];
      setMessages(newChat);
    } else {
      setNewMessage((prev) => prev + e.emoji);
    }
    setSelectedEmoji(e.emoji);
    console.log("Selected Emoji:", e.emoji);
    setShowEmojiPicker(false);
  };
  // X l khi chn nh t my tnh v gi ngay lp tc
  const handleFileChange = async (e) => {
    const file = e.target.files[0];
    if (file) {
      try {
        const formData = new FormData();
        formData.append("image", file);
        formData.append("sender", loggedInUserName);
        formData.append("receiver", currentFriend.name);

        // Gi nh ln server thng qua WebSocket
        const response = await sender(
          SEND_CHAT(props.friend.name, "", formData)
        );

        if (response && response.data && response.data.imageUrl) {
          // Lu tin nhn nh vo Redux
          const imageUrl = response.data.imageUrl; // URL ca nh sau khi lu trn server
          dispatch(
            saveImageURL({
              name: currentFriend.name,
              imageUrl: imageUrl,
            })
          );

          // Cp nht tin nhn hin th trn giao din
          const newChatWithImage = [
            ...messages,
            { text: imageUrl, isSentByUser: true }, // Hin th nh bng URL
          ];
          setMessages(newChatWithImage);
        } else {
          console.error("Response data or imageUrl is undefined:", response);
          // X l trng hp response khng c d liu mong i
        }
      } catch (error) {
        console.error("Error sending image:", error);
        // X l li khi gi nh (nu cn)
      } finally {
        setSelectedFile(null); // t li selectedFile v null sau khi gi
      }
    }
  };

  return (
    <div className="chatContainer">
      <div className="header">
        <div className="item">
          <div className="img">
            <img src="img/p1.jpg" alt="avatar" />
          </div>
          <div className="name">
            <div className="info">
              <span>{props.friend.name}</span>
            </div>
            <div className="icons">
              <FontAwesomeIcon className="icon" icon={faPhone} />
              <FontAwesomeIcon className="icon" icon={faVideo} />
              <FontAwesomeIcon className="icon" icon={faBars} />
            </div>
          </div>
        </div>
      </div>
      <div className="main">
        {console.log(messages)}
        {messages.map((message, index) => (
          <Message
            key={index}
            text={message.text}
            isSentByUser={message.isSentByUser}
          />
        ))}
      </div>
      {showEmojiPicker && (
        <div className="emojiPicker">
          <EmojiPicker onEmojiClick={handleEmoji} />
        </div>
      )}
      <div className="footer">
        <input
          className="input"
          type="text"
          placeholder="Nhp tin nhn..."
          value={newMessage}
          onChange={(e) => setNewMessage(e.target.value)}
          onKeyDown={handleKeyDown} // Bt s kin Enter
          ref={inputRef}
        />
        <div className="sendItem">
          <FontAwesomeIcon
            className="icon"
            icon={faFaceSmile}
            onClick={() => setShowEmojiPicker((prev) => !prev)}
          />
          <label htmlFor="fileInput">
            <FontAwesomeIcon className="icon" icon={faPaperclip} />
          </label>
          <input
            id="fileInput"
            type="file"
            onChange={handleFileChange}
            style={{ display: "none" }}
          />
          <div className="send" onClick={handleSendMessages}>
            <span>Gi</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default forwardRef(Chat);
