import React, {useState} from 'react';
import styles from './styles.module.css';
import { useNavigate } from 'react-router-dom'; // S dng React Router

const ChangeNumber= () => {
    const navigate = useNavigate();
    const [modalVisible, setModalVisible] = useState(false);


    const handleContinue = () => {
        setModalVisible(true);
    };

    const handleCancel = () => {
        navigate("/info")
    }
    const closeModal = () => {
        setModalVisible(false);
    };

    const redirectToAccountInfo = () => {
        navigate("/info")
    };

    const handleLogout = () => {
        // Xa thng tin ng nhp khi localStorage hoc gi API  ng xut
        localStorage.removeItem('token');

        // iu hng v trang ng nhp
        navigate('/');
    };
    const informationForward = () =>{
        navigate('/info');
    }

    const protectAccForward = () =>{
        navigate('/acc_protect');
    }


    const historyForward = () =>{
        navigate('/history');
    }

    return (
        <div className={styles.contain}>
            <div className={styles.main}>
                <div className={styles.left}>
                    <div className={styles.container}>
                        <div className={styles.avatar}>

                            <div className={styles.avatarContainer}>

                                <div className={styles['back-arrow']} onClick={() => navigate('/home')}>
                                    &#8592; {}
                                </div>
                                <img
                                    className={styles.avatar}
                                    src="https://www.w3schools.com/howto/img_avatar.png"
                                    alt="Avatar"
                                /></div>
                        </div>
                    </div>

                    <div className={styles.function}>
                        <div className={styles['function-item']} onClick={informationForward}>

                            <h4>Thng tin ti khon</h4>
                            <span>Qun l thng tin ng nhp v thng tin c nhn</span>

                        </div>

                        <div className={styles['function-item']} onClick={protectAccForward}>
                            <h4>Bo v ti khon</h4>
                            <span>H tr bo v ti khon</span>
                        </div>
                        <div className={styles['function-item']} onClick={historyForward}>
                            <h4>Nht k hot ng</h4>
                            <span>Lch s hot ng ca ti khon</span>
                        </div>
                        <div className={styles['function-item']} onClick={handleLogout}>
                            <h4>ng xut</h4>
                        </div>
                    </div>
                </div>
                <div className={styles.right}>
                    <div className={styles.title}>
                        <h2>Thay i mt khu</h2>
                    </div>
                    <div className={styles['title-info']}>
                        <h3>Thng tin ng nhp</h3>
                        <div className={styles['info-item']}>
                            <label htmlFor="number">S in thoi mi :</label>
                        </div>
                        <div className={styles['info-item']}>
                            <label htmlFor="password">Nhp m OTP :</label>
                        </div>

                        <div className={styles['button-confirm']}>
                            <button className={styles.continue} onClick={handleContinue}>Tip tc</button>
                            <button className={styles.cancel} onClick={handleCancel}>Hy b</button>
                        </div>
                        {modalVisible && (
                            <div id="myModal" className={styles.modal} style={{display: 'flex'}}>
                                <div className={styles['modal-content']}>
                                <span className={styles.close} onClick={closeModal}>&times;</span>
                                    <p>Cp nht thnh cng!</p>
                                    <button onClick={redirectToAccountInfo}>OK</button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ChangeNumber;